.catalog-item {
  position: relative;

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 21px 0 24px;

  @media (--not-narrow-vp) {
    --background-offset: 73px;

    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 36px 39px;

    background-image: linear-gradient(
      to bottom,
      transparent 0%,
      transparent var(--background-offset),
      var(--bg-color-lighten) var(--background-offset),
      var(--bg-color-lighten) 100%
    );
  }

  @media (--wide-vp) {
    --background-offset: 82px;

    padding: 0 38px 40px 38px;
  }

  &::before {
    @media (--narrow-vp) {
      content: "";

      position: absolute;
      top: 0;
      right: 0;
      left: 0;

      display: block;
      height: 1px;

      background-color: var(--border-color-light);
      box-shadow: 30px 0 0 0 var(--border-color-light), -30px 0 0 0 var(--border-color-light);
    }
  }
}

.catalog-item__title {
  margin: 0 0 16px 0;

  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--text-color-dark);
  text-align: left;
  text-transform: uppercase;

  @media (--not-narrow-vp) {
    margin-bottom: 21px;

    font-size: 20px;
    line-height: 24px;
    text-align: center;
  }

  @media (--wide-vp) {
    margin-bottom: 26px;

    line-height: 21px;
  }
}

.catalog-item__image {
  grid-column: 1 / 2;
  grid-row: 1 / 5;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;

  @media (--not-narrow-vp) {
    order: -1;
    width: 168px;
    height: 200px;
    margin-bottom: 34px;
  }

  @media (--wide-vp) {
    margin-bottom: 45px;
  }
}

.catalog-item__img {
  display: block;
}

.catalog-item__link {
  display: block;

  color: inherit;
  text-decoration: none;
  width: 80px;

  @media (--not-narrow-vp) {
    width: 145px;
  }
}

.catalog-item__parameters {
  width: 100%;
  margin: 0;
  padding: 0;
}

.catalog-item__parameter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 2px;

  font-size: 12px;
  line-height: 14px;

  @media (--not-narrow-vp) {
    position: relative;

    margin-bottom: 16px;

    font-size: 16px;
    line-height: 16px;
  }

  &::before {
    margin-right: auto;
    vertical-align: middle;
  }

  &::after {
    @media (--not-narrow-vp) {
      content: "";

      position: absolute;
      right: 0;
      bottom: -7px;
      left: 0;

      width: 100%;
      height: 1px;
      background-color: var(--border-color-darken);
    }
  }
}

.catalog-item__price {
  margin-bottom: 5px;
}

.catalog-item__order {
  grid-column: 1 / -1;
  width: 100%;
  margin-top: 12px;
  padding: 10px 20px;

  font-size: 16px;
  line-height: 20px;

  @media (--not-narrow-vp) {
    margin-top: 26px;
  }

  @media (--wide-vp) {
    margin-top: 24px;
  }
}
