@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", sans-serif;
}

*,
::before,
::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  object-fit: cover;
}

:focus {
  outline: 2px solid var(--text-color-accent);
  outline-offset: 2px;
}
@supports selector(:focus-visible) {
  :focus {
    outline: 0;
  }
}

:focus-visible {
  outline: 2px solid var(--text-color-accent);
  outline-offset: 2px;
}

:root {
  --text-color-accent: #000000;
  --text-color-main: #444444;
  --text-color-light: #666666;
  --text-color-dark: #222222;
  --text-color-darken: #f2f2f2;
  --text-color-contrast: #ffffff;
  --bg-color-main: #ffffff;
  --bg-color-lighten: #f2f2f2;
  --bg-color-light: #ebebeb;
  --bg-color-dark: #eaeaea;
  --bg-color-darkest: #dcdcdc;
  --bg-color-accent: #68b738;
  --bg-color-accent-bright: #5eaa2f;
  --border-color-light: #e6e6e6;
  --border-color-lighten: #e7e7e7;
  --border-color-dark: #d9d9d9;
  --border-color-darken: #cdcdcd;
  --border-color-darkest: #bababa;
  --border-color-black: #444444;
  --border-color-alert: #ff8282;
}

@custom-media --narrow-vp (width <= 767.9px);
@custom-media --not-narrow-vp (width >= 768px);
@custom-media --middle-vp (768px <= width < 1279.9px);
@custom-media --wide-vp (width >= 1280px);
@custom-media --not-wide-vp (width <= 1279.9px);
.page {
  height: 100%;
  font-family: var(--main-font);
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: var(--text-color-main);
  word-wrap: break-word;
  overflow-wrap: break-word;
}
@media (--not-narrow-vp) {
  .page {
    font-size: 16px;
    line-height: 24px;
  }
}

.page__body {
  --scrollbar: calc(100vw - 100%);
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
}

.page__main {
  flex-grow: 1;
}

.page__centerer {
  --content-indent: calc(20px + (10vw - 32px) / 4.48);
  margin-inline: var(--content-indent) calc(var(--content-indent) - var(--scrollbar));
}
@media (--narrow-vp) {
  .page__centerer {
    --content-indent: calc(20px + (10vw - 32px) / 4.48);
  }
}
@media (--middle-vp) {
  .page__centerer {
    --content-indent: 30px;
  }
}
@media (--wide-vp) {
  .page__centerer {
    margin: 0 auto;
    width: 1220px;
  }
}

.page__title {
  margin-bottom: 4px;
}
@media (--not-narrow-vp) {
  .page__title {
    margin-bottom: 90px;
  }
}
@media (--wide-vp) {
  .page__title {
    margin-bottom: 84px;
  }
}

.page__banner {
  margin-bottom: 20px;
}
@media (--not-narrow-vp) {
  .page__banner {
    margin-bottom: -8px;
  }
}
@media (--wide-vp) {
  .page__banner {
    margin-bottom: 82px;
  }
}

.page__modules {
  margin-bottom: 23px;
}
@media (--not-narrow-vp) {
  .page__modules {
    margin-bottom: 43px;
  }
}
@media (--wide-vp) {
  .page__modules {
    margin-bottom: 72px;
  }
}

@media (--not-narrow-vp) {
  .page__features {
    margin-bottom: 43px;
  }
}
@media (--wide-vp) {
  .page__features {
    margin-bottom: 37px;
  }
}

.page__catalog {
  margin-bottom: 13px;
}
@media (--not-narrow-vp) {
  .page__catalog {
    margin-bottom: 44px;
  }
}
@media (--wide-vp) {
  .page__catalog {
    margin-bottom: 50px;
  }
}

.page__additional {
  margin-bottom: 37px;
}
@media (--not-narrow-vp) {
  .page__additional {
    margin-bottom: 76px;
  }
}
@media (--wide-vp) {
  .page__additional {
    margin-bottom: 86px;
  }
}

.page__brief {
  margin-bottom: 30px;
}
@media (--not-narrow-vp) {
  .page__brief {
    margin-bottom: 74px;
  }
}
@media (--wide-vp) {
  .page__brief {
    margin-bottom: 84px;
  }
}

.page__footer {
  margin-top: auto;
}

.page__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: block;
  width: fit-content;
  padding: 10px 20px;
  border: none;
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: var(--text-color-contrast);
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  background-color: var(--bg-color-accent);
  cursor: pointer;
}
@media (--not-narrow-vp) {
  .button {
    padding: 17px 20px;
    font-size: 20px;
    line-height: 20px;
  }
}
.button:hover {
  background-color: var(--bg-color-accent-bright);
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: var(--bg-color-accent-bright);
}
.button--size_wide {
  width: 100%;
}
.button--color_gray {
  color: var(--text-color-main);
  background-color: var(--bg-color-lighten);
}
.button--color_gray:hover {
  background-color: var(--bg-color-light);
}
.button--color_gray:active {
  color: rgba(68, 68, 68, 0.3);
  background-color: var(--bg-color-light);
}
@media (--wide-vp) {
  .button--size_big {
    height: 54px;
    padding: 17px 20px;
    font-size: 20px;
  }
}

.page-title {
  padding-top: 27px;
  font-family: var(--accent-font);
  color: var(--text-color-accent);
}
@media (--not-narrow-vp) {
  .page-title {
    padding-top: 17px;
  }
}
@media (--wide-vp) {
  .page-title {
    padding-top: 46px;
  }
}
.page-title--subtitled {
  margin-bottom: 2px;
}
@media (--not-narrow-vp) {
  .page-title--subtitled {
    margin-bottom: 0;
    padding-top: 17px;
  }
}
@media (--middle-vp) {
  .page-title--subtitled {
    padding-right: 40px;
    padding-left: 40px;
  }
}
@media (--wide-vp) {
  .page-title--subtitled {
    padding-top: 47px;
  }
}

.page-title__header {
  margin-top: 0;
  margin-bottom: 41px;
  font-family: inherit;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}
@media (--not-narrow-vp) {
  .page-title__header {
    margin-bottom: 54px;
    font-size: 60px;
    line-height: 60px;
  }
}
@media (--wide-vp) {
  .page-title__header {
    margin-bottom: 56px;
  }
}

.page-title__text {
  margin-top: 0;
  margin-bottom: 36px;
  font-family: inherit;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}
@media (--not-narrow-vp) {
  .page-title__text {
    max-width: 330px;
    margin-bottom: 66px;
    font-size: 20px;
    line-height: 30px;
  }
}

.section-title {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: var(--text-color-accent);
}
@media (--not-narrow-vp) {
  .section-title {
    font-size: 60px;
    line-height: 60px;
  }
}

.section-title__text {
  --air-gap: 22px;
  --top-offset: 15px;
  --line-color: var(--bg-color-accent);
  display: inline-block;
  width: fit-content;
}
.section-title__text--decoration {
  position: relative;
  font-size: 24px;
  line-height: 24px;
}
@media (--not-narrow-vp) {
  .section-title__text--decoration {
    --air-gap: 37px;
    --top-offset: 22px;
    margin-left: var(--air-gap);
    font-size: 36px;
    line-height: 36px;
  }
}
@media (--wide-vp) {
  .section-title__text--decoration {
    margin-left: 0;
  }
}
.section-title__text--decoration::before {
  content: "";
  position: absolute;
  top: var(--top-offset);
  right: calc(100% + var(--air-gap));
  display: block;
  width: calc(100vw - 100% - var(--air-gap));
  height: 2px;
  background-color: var(--line-color);
  box-shadow: calc(100vw + var(--air-gap)) 0 0 0 var(--line-color);
}
@media (--narrow-vp) {
  .section-title__text--line {
    width: 182px;
  }
}

@media (--not-narrow-vp) {
  .main-header {
    padding-top: 29px;
    padding-bottom: 43px;
  }
}
@media (--wide-vp) {
  .main-header {
    padding-top: 53px;
    padding-bottom: 28px;
  }
}

.main-header__container {
  position: relative;
}
@media (--not-narrow-vp) {
  .main-header__container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
}

.main-header__logo {
  flex-shrink: 0;
}

@media (--not-narrow-vp) {
  .main-header__nav {
    flex-grow: 1;
    padding-top: 18px;
  }
}

.main-nav {
  font-family: var(--accent-font);
}
@media (--narrow-vp) {
  .main-nav {
    border-bottom: 1px solid var(--border-color-light);
    box-shadow: 30px 0 0 0 var(--border-color-light), -30px 0 0 0 var(--border-color-light);
  }
}
@media (--not-narrow-vp) {
  .main-nav {
    padding: 0;
    background: none;
  }
}
.main-nav--no-js .main-nav__toggler {
  display: none;
}
.main-nav--no-js .main-nav__list {
  position: static;
}

.main-nav__toggler {
  --icon-color: var(--text-color-accent);
  position: absolute;
  top: 22px;
  right: 0;
  width: 24px;
  height: 18px;
  border-radius: 0;
  border: none;
  background-color: var(--bg-color-main);
  cursor: pointer;
}
@media (--not-narrow-vp) {
  .main-nav__toggler {
    display: none;
  }
}
.main-nav__toggler::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 25px;
  height: 1.5px;
  background-color: var(--icon-color);
  transform: translate(-50%, -50%) rotate(45deg);
}
.main-nav__toggler::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 25px;
  height: 1.5px;
  background-color: var(--icon-color);
  transform: translate(-50%, -50%) rotate(-45deg);
  box-shadow: none;
}
.main-nav__toggler--closed::before {
  top: 0;
  left: 0;
  width: 100%;
  transform: none;
}
.main-nav__toggler--closed::after {
  top: 8px;
  left: 0;
  width: 100%;
  box-shadow: 0 8px 0 0 var(--icon-color);
  transform: none;
}

.main-nav__list {
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 10;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (--not-narrow-vp) {
  .main-nav__list {
    position: static;
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    margin-left: 20px;
  }
}
@media (--narrow-vp) {
  .main-nav__list--closed {
    display: none;
  }
}

@media (--narrow-vp) {
  .main-nav__item {
    --nav-item-bg-color: var(--bg-color-main);
    background-color: var(--nav-item-bg-color);
    box-shadow: 30px 0 0 0 var(--nav-item-bg-color), -30px 0 0 0 var(--nav-item-bg-color);
  }
}
@media (--not-narrow-vp) {
  .main-nav__item {
    background: none;
  }
  .main-nav__item:not(:first-child) {
    margin-left: 35px;
  }
}

.main-nav__link {
  position: relative;
  display: block;
  width: 100%;
  padding: 24px 20px 22px;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: var(--text-color-accent);
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
}
@media (--not-narrow-vp) {
  .main-nav__link {
    position: relative;
    padding: 0;
    border: none;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
  .main-nav__link[aria-current=page]::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -7px;
    left: 0;
    display: block;
    height: 2px;
    background-color: var(--bg-color-accent);
  }
}
@media (--not-narrow-vp) and (--wide-vp) {
  .main-nav__link {
    font-size: 20px;
    line-height: 30px;
  }
}
.main-nav__link[aria-current=page] {
  pointer-events: none;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
@media (--narrow-vp) {
  .main-nav__link::before {
    --nav-item-border: var(--border-color-light);
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    display: block;
    height: 1px;
    background-color: var(--nav-item-border);
    box-shadow: 30px 0 0 0 var(--nav-item-border), -30px 0 0 0 var(--nav-item-border);
  }
}

@media (--wide-vp) {
  .main-nav--index .main-nav__link {
    color: var(--text-color-contrast);
  }
  .main-nav--index .main-nav__link[aria-current=page]::after {
    background-color: var(--bg-color-main);
  }
}

.logo {
  flex-grow: 1;
  flex-shrink: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0 10px;
  text-decoration: none;
}
.logo[aria-current=page] {
  pointer-events: none;
}
@media (--not-narrow-vp) {
  .logo {
    flex-grow: 0;
    justify-content: flex-start;
    width: 174px;
    height: 52px;
    padding: 0;
  }
}
@media (--wide-vp) {
  .logo {
    width: 202px;
    height: 60px;
    padding-top: 15px;
  }
}
.logo--footer {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 39px 20px 32px;
  text-align: center;
}
@media (--not-narrow-vp) {
  .logo--footer {
    justify-content: flex-start;
    padding: 0;
    text-align: left;
  }
}
@media (--narrow-vp) {
  .logo--header::after {
    content: "";
    width: 34px;
  }
}
.logo:hover {
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}

.logo__image {
  width: 34px;
  height: 38px;
}
@media (--not-narrow-vp) {
  .logo__image {
    width: 63px;
    height: 56px;
    margin-right: 12px;
  }
}
@media (--wide-vp) {
  .logo__image {
    width: 70px;
    height: 70px;
  }
}

@media (--wide-vp) {
  .logo__text {
    width: 118px;
    height: 22px;
  }
}
.logo__text--footer {
  width: 128px;
  height: 24px;
}

.main-footer {
  background-color: var(--bg-color-lighten);
}

.main-footer__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (--not-narrow-vp) {
  .main-footer__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-content: center;
    column-gap: 20px;
    padding: 50px 0 46px;
  }
}
@media (--wide-vp) {
  .main-footer__container {
    padding: 45px 0;
  }
}

@media (--narrow-vp) {
  .main-footer__item {
    width: 100%;
  }
  .main-footer__item:not(:last-child) {
    border-bottom: 1px solid var(--border-color-dark);
  }
}

.social {
  padding: 10px 20px;
}
@media (--not-narrow-vp) {
  .social {
    padding: 0;
  }
}

.social__list {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px 0 0;
  padding: 0;
  list-style-type: none;
}
@media (--not-narrow-vp) {
  .social__list {
    margin-right: 7px;
  }
}

.social__item {
  margin: 10px 15px;
}
@media (--not-narrow-vp) {
  .social__item {
    margin: 10px;
  }
}

.social__link {
  --icon-width: 21px;
  display: grid;
  width: var(--icon-width);
  height: var(--icon-width);
}
@media (--not-narrow-vp) {
  .social__link {
    --icon-width: 30px;
  }
}
.social__link:hover::before {
  background: var(--bg-color-accent);
}
.social__link:active {
  opacity: 0.3;
}
.social__link::before {
  content: "";
  grid-area: 1/1/2/2;
  display: block;
  width: var(--icon-width);
  height: var(--icon-width);
  background: var(--text-color-light);
  mask: var(--icon-path) no-repeat center/cover;
}
.social__link--vk {
  --icon-path: url("../icons/sprite.svg#vk");
}
.social__link--insta {
  --icon-path: url("../icons/sprite.svg#instagram");
}
.social__link--fb {
  --icon-path: url("../icons/sprite.svg#facebook");
}

.creators {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 22px 0;
  font-size: 16px;
  line-height: 18px;
  color: var(--text-color-main);
  text-decoration: none;
}
@media (--not-narrow-vp) {
  .creators {
    justify-content: flex-end;
    padding: 0 0 8px 0;
  }
}
@media (--wide-vp) {
  .creators {
    padding: 6px 0 0 0;
  }
}
.creators:hover::after {
  background-color: var(--bg-color-accent);
}
.creators:active {
  opacity: 0.3;
}
.creators::after {
  content: "";
  display: block;
  width: 27px;
  height: 34px;
  margin-left: 22px;
  background-color: currentColor;
  mask: url("../icons/sprite.svg#logo-academy") no-repeat center/cover;
}

.banner {
  position: relative;
}
@media (--wide-vp) {
  .banner {
    position: static;
  }
}
.banner::before {
  --banner-gradient: linear-gradient(to bottom, rgba(104, 183, 56, 0.85) 0%, rgba(104, 183, 56, 0.85) 100%);
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 0;
  display: block;
  width: 100%;
  height: 302px;
  background-color: var(--bg-color-accent);
  background-image: var(--banner-gradient), url("../img/bg-banner-s@1x.jpg");
  background-position: center, right 101% center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (min-resolution: 2dppx) {
  .banner::before {
    background-image: var(--banner-gradient), url("../img/bg-banner-s@2x.jpg");
  }
}
@media (--middle-vp) {
  .banner::before {
    display: none;
  }
}
@media (--wide-vp) {
  .banner::before {
    top: 0;
    left: 50%;
    z-index: -1;
    width: 50%;
    height: 693px;
    background-image: var(--banner-gradient), url("../img/bg-banner-l@1x.jpg");
  }
}
@media (--wide-vp) and (min-resolution: 2dppx) {
  .banner::before {
    background-image: var(--banner-gradient), url("../img/bg-banner-l@2x.jpg");
  }
}

.banner__container {
  position: relative;
  z-index: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (--not-narrow-vp) {
  .banner__container {
    align-items: flex-start;
    background-image: none;
  }
}

.banner__content {
  padding-top: 27px;
  font-family: var(--accent-font);
  font-weight: 400;
  text-align: center;
  color: var(--text-color-contrast);
}
@media (--not-narrow-vp) {
  .banner__content {
    width: 440px;
    margin-left: 87px;
    padding-top: 17px;
    text-align: left;
    color: var(--text-color-accent);
  }
}
@media (--wide-vp) {
  .banner__content {
    margin-left: 80px;
    padding-top: 82px;
  }
}

.banner__title {
  margin-top: 0;
  margin-bottom: 25px;
  font-weight: inherit;
  font-size: 36px;
  line-height: 36px;
  color: inherit;
}
