  padding: 59px 58px 43px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--text-color-contrast);
  text-decoration: none;
  background-color: var(--bg-color-accent);
  background-image: url("../img/gift-s@1x.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
@media (min-resolution: 2dppx) {
  .additional-all {
    background-image: url("../img/gift-s@2x.jpg");
  }
}
@media (--not-narrow-vp) {
  .additional-all {
    flex-direction: row;
    min-height: 200px;
    padding: 57px 124px 60px 77px;
    background-position: center top 60%;
  }
}
@media (--middle-vp) {
  .additional-all {
    background-image: url("../img/gift-m@1x.jpg");
    background-size: cover;
  }
}
@media (--middle-vp) and (min-resolution: 2dppx) {
  .additional-all {
    background-image: url("../img/gift-m@2x.jpg");
  }
}
@media (--wide-vp) {
  .additional-all {
    flex-direction: column;
    padding: 55px 40px 40px;
    background-position: right center;
  }
}
.additional-all::before {
  content: "";
  display: block;
  width: 80px;
  height: 80px;
  background-color: var(--bg-color-main);
  mask: url("../icons/sprite.svg#gift");
}

.additional-all__text {
  max-width: 165px;
  text-align: center;
}
@media (--middle-vp) {
  .additional-all__text {
    max-width: 162px;
    padding-top: 5px;
    text-align: left;
  }
}