.contact {
  @media (--wide-vp) {
    display: grid;
  }
}

.contact__container {
  @media (--wide-vp) {
    z-index: 2;

    grid-area: 1 / 1 / 2 / 2;
    height: 400px;
    padding-top: 95px;

    pointer-events: none;
  }
}

.contact__content {
  --contact-bg-color: var(--bg-color-main);

  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 17px 0;

  background-color: var(--contact-bg-color);

  @media (--not-narrow-vp) {
    align-items: stretch;

    padding: 55px 40px;
  }

  @media (--not-wide-vp) {
    box-shadow: 30px 0 0 0 var(--contact-bg-color), -30px 0 0 0 var(--contact-bg-color);
  }

  @media (--wide-vp) {
    width: 565px;
    padding: 55px 77px;

    pointer-events: initial;
  }

  &--inner {
    @media (--not-wide-vp) {
      --contact-bg-color: var(--bg-color-lighten);
    }
  }
}

.contact__title {
  flex-shrink: 0;
  width: 140px;
  margin: 0;
  padding: 9px 0;

  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: var(--text-color-accent);

  @media (--not-narrow-vp) {
    width: 170px;
    padding: 5px 0;

    font-size: 20px;
    line-height: 26px;
  }
}

.contact__address {
  flex-shrink: 0;
  width: 140px;

  font-size: 14px;
  line-height: 20px;

  @media (--not-narrow-vp) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    width: 160px;
    padding-bottom: 7px;

    font-size: 16px;
    text-align: right;
  }

  @media (--wide-vp) {
    align-items: flex-start;
    text-align: left;
  }
}

.contact__place {
  margin: 0;

  @media (--wide-vp) {
    margin-bottom: 10px;
  }
}

.contact__city {
  margin: 0;

  @media (--wide-vp) {
    margin-bottom: 7px;
  }
}

.contact__map {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  justify-items: center;
  align-items: center;
  height: 362px;

  background-color: var(--bg-color-accent);

  @media (--not-narrow-vp) {
    height: 400px;
  }

  @media (--wide-vp) {
    z-index: 0;

    grid-area: 1 / 1 / 2 / 2;
  }
}

.contact__map-interactive {
  width: 100%;
  height: 100%;
  border: none;
}
